@mixin rob {
	font-family: 'Roboto', sans-serif; }
@include font(Rosamunda, Rosamunda-One);
@mixin roca {
	font-family: Rosamunda, sans-serif; }

$white: #fff;
$black: #000;
$red: #df0327;
$red2: #e92701;
$red3: #fcbe95;
$red5: #f7c3a1;
$purple: #651960;
$gray: #242424;


body {
	background: $white;
	line-height: 1.4;
	@include font-smoothing;
	@include rob;
	font-size: 2vh; }

a {
	text-decoration: none; }
h1,h2,h3,h4,h5,h6 {
	font-weight: normal; }
.title-lg {
	color: #df0327;
	@include roca;
	font-size: 10vh; }
.decor-text {
	color: $purple;
	font-size: 10vh;
	&--fixed-size {
		font-size: 30px; } }
.two-col {
	height: 100%;
	@include clr;
	&__left {
		float: left; }
	&__right {
		float: right; } }
.article {
	color: $gray;
	p {
		margin-bottom: 2vh; } }
