html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: Rosamunda;
  src: url("../fonts/Rosamunda-One.woff2") format("woff2"), url("../fonts/Rosamunda-One.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background: #fff;
  line-height: 1.4;
  font-smoothing: antialiased;
  font-family: 'Roboto', sans-serif;
  font-size: 2vh;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

.title-lg {
  color: #df0327;
  font-family: Rosamunda, sans-serif;
  font-size: 10vh;
}

.decor-text {
  color: #651960;
  font-size: 10vh;
}

.decor-text--fixed-size {
  font-size: 30px;
}

.two-col {
  height: 100%;
}

.two-col:after {
  content: " ";
  display: table;
  clear: both;
}

.two-col__left {
  float: left;
}

.two-col__right {
  float: right;
}

.article {
  color: #242424;
}

.article p {
  margin-bottom: 2vh;
}

.l {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
  height: 100%;
}

.block {
  display: flex;
}

#fp-nav {
  position: fixed;
  z-index: 20;
  width: 100%;
  bottom: 6vh;
  text-align: center;
  font-size: 0;
}

#fp-nav li {
  display: inline-block;
  vertical-align: top;
  margin: 0 19px;
}

#fp-nav a {
  display: block;
  position: relative;
  appearance: none;
  background: #fcbe95;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  transition: all 0.2s ease;
}

#fp-nav a.active {
  background: #e92701;
}

#fp-nav a:before {
  content: '';
  position: absolute;
  top: -4px;
  left: -4px;
  bottom: -4px;
  right: -4px;
}

.section {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.section__inner {
  height: 100%;
}

.section__bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -5;
}

.top-lines {
  position: absolute;
  z-index: 0;
  top: 30%;
}

.top-lines:before, .top-lines:after {
  content: '';
  position: absolute;
  width: 4000px;
  height: 1px;
  background: #f7c3a1;
}

.top-lines:before {
  left: 0;
  transform: translateX(-100%);
}

.top-lines:after {
  right: 0;
  transform: translateX(100%);
}

.top-lines--red:before, .top-lines--red:after {
  background: #e92701;
}

.top-lines--full:before {
  left: 50%;
}

.top-lines--full:after {
  right: 50%;
}

.section-content {
  height: 100%;
  position: relative;
}

.section-content__head {
  text-align: center;
}

.section-content__head-img {
  display: inline-block;
  vertical-align: top;
  position: relative;
  font-size: 0;
}

.section-content__head-img img {
  width: auto;
  height: 20vh;
  position: relative;
  z-index: 2;
}

.first-section__left {
  width: 42.80702%;
}

.first-section__right {
  width: 57.01754%;
  padding-right: 100px;
  text-align: right;
  padding-top: 7vh;
}

@media only screen and (max-width: 1700px) {
  .first-section__right {
    padding-right: 150px;
  }
}

.first-section__we-img {
  font-size: 0;
  text-align: right;
}

.first-section__we-img img {
  height: 77vh;
  max-height: 779px;
}

.first-section__decor-text {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  display: inline-block;
  vertical-align: top;
  height: 172px;
  line-height: 172px;
  padding: 0 120px;
  position: relative;
}

@media only screen and (max-width: 1700px) {
  .first-section__decor-text {
    padding: 0 80px;
  }
}

.first-section__decor-text:before, .first-section__decor-text:after {
  content: '';
  position: absolute;
  height: 2px;
  width: 80px;
  background: #fcbe95;
  top: 50%;
  transform: translateY(-50%);
}

@media only screen and (max-width: 1700px) {
  .first-section__decor-text:before, .first-section__decor-text:after {
    width: 60px;
  }
}

.first-section__decor-text:before {
  left: 0;
}

.first-section__decor-text:after {
  right: 0;
}
